<template lang="">
    <div id="scan_qrcode" ref="scan_qrcode">
        <div>
            <video ref="video" class="video" @dblclick="end_scan"></video>
        </div>
        <div class="scan_bar_container" :style="style.scan_bar_container_style">
            <img src="../assets/images/scan-bar-m.png" alt="">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            ratio: 2.74,
            dimension: {
                background_img_width: 1080,
                background_img_height: 3463,
            },
            style:{
                scan_bar_container_style: {},
                scan_bar_start_position:"",
                scan_bar_cut_position: "",
            },
            constraints: {
                // video属性设置，手机的相机默认是横置拍摄，所以要constraints的width和height要调换
                video: { 
                    width: 2400, 
                    height: 1800,
                    facingMode: { exact: "environment" } 
                 },
                // audio属性设置
                audio: false
            }
        }
    },
    methods:{
        CR(len){
            return (len / this.ratio) + "px"
        },
        end_scan(){
            // 结束所有视频流
            this.$stream.getTracks().forEach(function(track){
                track.stop()
            })
            this.$refs.scan_qrcode.style.display = "none"
            this.$router.push({name: 'place'})
        },
        open_camera(){
            const self = this
            this.constraints.video.width = window.screen.availHeight
            this.constraints.video.height = window.screen.availWidth
            clearInterval(self.roll_timer)
            navigator.mediaDevices.getUserMedia(this.constraints).then(function(mediaStream){
                self.$stream = mediaStream
                self.$refs.video.srcObject = mediaStream
                self.$refs.video.onloadedmetadata = function() {
                    self.$refs.scan_qrcode.style.display = "block"
                    self.$refs.video.play()
                    self.roll_scan_bar(3)
                };
            })
        },
        // compute ratio and set css
        init_style(){
            // get_ratio
            this.ratio = this.dimension.background_img_width / window.outerWidth
            /*this.style.video_style = {
                height: window.outerHeight
            }*/
            // style
            this.get_scan_bar_container_style()

        },
        roll_scan_bar(distance){
            this.style.scan_bar_container_style.display = "block"
            let cut_position = parseInt(this.style.scan_bar_cut_position)
            let scan_bar_height = parseInt(this.style.scan_bar_cut_position) - parseInt(this.style.scan_bar_start_position)
            this.roll_timer = setInterval(() => {
                let top_position =  parseInt(this.style.scan_bar_container_style.top)
                if(top_position >= cut_position ){
                    this.style.scan_bar_container_style.top = this.style.scan_bar_start_position
                } else {
                    let top_distance = top_position - parseInt(this.style.scan_bar_start_position)
                    this.style.scan_bar_container_style.top = top_position + distance + "px"
                    let opacity = 0.7 - Math.abs(0.5 - (top_distance / scan_bar_height)).toFixed(2)
                    this.style.scan_bar_container_style.opacity = opacity >= 0.35 ? 0.7 : opacity
                }
            },20)
            
        },
        get_scan_bar_container_style(){
            this.style.scan_bar_container_style = {
                top: this.CR(400),
            }
            this.style.scan_bar_start_position = this.CR(400)
            this.style.scan_bar_cut_position = this.CR(1250)
        },
    },
    mounted(){
        window.onresize = () => {
            this.init_style()
        }
        this.init_style()
    }
}
</script>
<style lang="less" scoped>
    #scan_qrcode{
        position: absolute;
        top: 0;
        width: 100%;
        .scan_bar_container{
            display: none;
            position: fixed;
            width: 88%;
            left: 50%;
            transform: translateX(-50%);
            img{
                width: 100%;
                opacity: 0.7;
            }
        }
        .video{
            position: fixed;
            width: 100%;
        }
    }
</style>