<template lang="">
    <div id="app-cover">
        <img src="../assets/images/cover.png">
        <div class="progress" :style="style.progress_style">
            <el-progress type="circle" :percentage="percentage" :style="style.percentage" :stroke-width="5" :show-text="false" color="#07c160"></el-progress>
        </div>
    </div>
</template>
<script>
import { ElProgress } from 'element-plus'
export default {
    components: {
        ElProgress
    },
    data(){
        return {
            ratio: 2.74,
            dimension: {
                background_img_width: 1080,
                background_img_height: 2400,
            },
            style: {
                progress_style: {},
                percentage: {}
            },
            percentage: 5
        }
    },
    methods: {
        CR(len){
          return (len / this.ratio) + "px"
        },
        init_style(){
            // get_ratio
            this.ratio = this.dimension.background_img_width / window.outerWidth
            // style
            this.change_percentage()
            this.get_progress_style()
        },
        get_progress_style(){
            this.style.progress_style = {
                top: this.CR(970),
                height: this.CR(117),
                width: this.CR(117),
            }
        },
        change_percentage(){
            let degree = 0
            const timer = setInterval(() => {
                let next_percentage = this.percentage + parseInt(Math.random()*1.8)
                if(next_percentage >= 100){
                    this.percentage = 100
                    clearInterval(timer)
                    this.$router.push({name: 'health_page'})
                    //this.$router.go("/health_page")
                } else {
                    this.percentage = next_percentage
                    degree += 5
                    this.style.percentage = {
                        transform: `rotate(${degree}deg)`,
                    }
                }
            },15)
        }
    },
    mounted(){
        window.onresize = () => {
            this.init_style()
        }
        this.init_style()
    }
}
</script>
<style lang="less" scoped>
    #app-cover{
        img{
            display: block;
            width: 100%;
        }
        .progress{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
        .el-progress{
            width: 100%;
            height: 100%;
            transform-origin: center;
        }
        /deep/ .el-progress-circle{
            width: 100% !important;
            height: 100% !important;
        }
    }
</style>