<template lang="">
    <div id="code_page">
        <img class="bg" src="../assets/images/fullscreen.png">
        <div class="slogan" :style="style.slogan_style">
            <span class="slogan_container" ref="slogan_container">
                <span class="slogan-item">
                    <span v-for="(slogan, index) in info.slogans" :key="index" >
                        <img src="../assets/images/hand.png" :style="style.hand">{{ slogan }}
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    </span>
                </span>
                <span class="slogan-item">
                    <span v-for="(slogan, index) in info.slogans" :key="index" >
                        <img src="../assets/images/hand.png" :style="style.hand">{{ slogan }}
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    </span>
                </span>
            </span>
        </div>
        <div class="personal_name" :style="style.personal_name_style">
            {{info.personal_name}}
        </div>
        <div class="personal_id" :style="style.personal_id_style">
            {{info.personal_id}}
        </div>
        <div class="marqueen" :style="style.marqueen_style">
            <img src="../assets/images/marqueen.png" ref="marqueen_bar" :style="style.marqueen_bar_style">
        </div>
        <div class="time_box" :style="style.time_box_style">
            <span class="h-m-s">
                {{timer.H_M}}:<span :style="style.time_second_style">{{timer.S}}</span>
            </span>
            <span class="date">
                {{timer.Y_M_D}}
            </span>
        </div>
        <div class="hours_certification" :style="style.hours_certification_style">
            {{timer.M_D}} 00时
        </div>
        <div class="update_button" :style="style.update.button" @click="update_event"></div>
        <div ref="update" class="update" :style="style.update.have_clicked ? style.update.after : style.update.before">
            {{style.update.click_label}}
        </div>
        <div class="multi_functions" :style="style.multi_functions_style">
            <div class="funtions_item" @click="$router.push({name:'hospital_page'})"></div>
            <div class="funtions_item"></div>
            <div class="funtions_item"></div>
            <div class="funtions_item"></div>
            <div class="funtions_item"></div>
            <div class="funtions_item"></div>
        </div>
        <!-- open camera -->
        <div class="camera_button" :style="style.camera_button_style" @click="$refs.camera.open_camera"></div>
        <scan_qrcode ref="camera"></scan_qrcode>
    </div>
</template>
<script>
import moment from "moment"
import scan_qrcode from "../components/scan-qrcode"

class Roll {
    constructor(element, move, distance) {
        this.counter = 0
        setInterval(async () => {
            if(this.counter >= await element.offsetWidth/2){
                this.counter = 0
            } else{
                move.left = (-this.counter) + "px"
                this.counter += distance
            }
        }, 10)
    }
}

export default {
    components:{
        scan_qrcode
    },
    data(){
        return {
            info: {
                personal_name:"*亮",
                personal_id:"51**************36",
                slogans: [
                    " 四川疾控健康提示（2022年7月24日20时）【点击查看详情】",
                    "【疫案说法】必看！疫情防控30种违法违规行为，10个案例告诉你法律后果！"
                ]
            },
            ratio: 2.74,
            dimension: {
                background_img_width: 1080,
                background_img_height: 3463,
            },
            style: {
                camera_button: {},
                marqueen_style: {},
                marqueen_bar_style: {
                    left: ""
                },
                slogan_style: {},
                hand: {},
                time_box_style: {},
                time_second_style: {},
                hours_certification_style:{},
                personal_name_style:{},
                personal_id_style:{},
                update: {
                    before: {},
                    after: {},
                    button: {},
                    have_clicked: false,
                    countdown: 60,
                    click_label: "点击更新"
                },
                multi_functions_style:{}
            },
            timer: {
                Y_M_D: moment().format('YYYY-MM-DD'),
                H_M: moment().format('HH:mm'),
                S: moment().format('ss'),
                M_D: moment().format('MM/DD')
            },
            constraints: {
                // video属性设置，手机的相机默认是横置拍摄，所以要constraintss的width和height要调换
                video: { 
                    width: 1920, 
                    height: 1080,
                    facingMode: { exact: "environment" } 
                 },
                // audio属性设置
                audio: false
            }
        }
    },
    methods: {
        // compute responsive retio
        CR(len){
          return (len / this.ratio) + "px"
        },
        // compute ratio and set css
        init_style(){
            // get_ratio
            this.ratio = this.dimension.background_img_width / window.outerWidth
            // style
            this.get_slogan_style()
            this.get_marqueen_style()
            this.get_hand_style()
            this.get_time_box_style()
            this.get_time_second_style()
            this.get_hour_certification_style()
            this.get_update_before()
            this.get_update_button()
            this.get_personal_name_style()
            this.get_personal_id_style()
            this.get_multi_functions()
            this.get_camera_button_style()
        },
        get_camera_button_style(){
            this.style.camera_button_style = {
                top: this.CR(1604),
                width: this.CR(614),
                height: this.CR(112),
            }
        },
        get_multi_functions(){
            this.style.multi_functions_style = {
                top: this.CR(2200),
                left: '50%',
                transform: 'translateX(-50%)',
                height: this.CR(620),
                width: this.CR(868),
            }
        },
        get_personal_id_style(){
            this.style.personal_id_style = {
                fontSize: this.CR(45),
                top: this.CR(777),
                left: this.CR(126),
            }
        },
        get_personal_name_style(){
            this.style.personal_name_style = {
                fontSize: this.CR(67),
                top: this.CR(690),
                left: this.CR(126),
            }
        },
        get_slogan_style(){
            this.style.slogan_style = {
                fontSize: this.CR(42),
                width: this.CR(805),
                height: this.CR(45),
                top: this.CR(537),
                left: this.CR(170),
            }
        },
        get_marqueen_style(){
            this.style.marqueen_style = {
                top: this.CR(1775),
                height: this.CR(16),
                width: this.CR(868),
            }
        },
        get_hand_style(){
            this.style.hand = {
                width: this.CR(50),
            }
        },
        get_time_box_style(){
            this.style.time_box_style = {
                top: this.CR(930),
                width: this.CR(660),
                fontSize: this.CR(65),
            }
        },
        get_time_second_style(){
            this.style.time_second_style = {
                fontSize: this.CR(72),
            }
        },
        get_hour_certification_style(){
            this.style.hours_certification_style = {
                top:  this.CR(1910),
                left: this.CR(122),
                fontSize: this.CR(26),
            }
        },
        get_update_before(){
            this.style.update.before = {
                top: this.CR(1980),
                left: this.CR(250),
                fontSize: this.CR(30),
                color: "#00a870"
            }
        },
        get_update_after(){
            this.style.update.after = {
                top: this.CR(1980),
                left: this.CR(205),
                fontSize: this.CR(30),
                color: "#80d4b8"
            }
        },
        get_update_button(){
            this.style.update.button = {
                top: this.CR(1970),
                left: this.CR(122),
                width: this.CR(393),
                height: this.CR(61),
                zIndex: 999
            }
        },
        update_event(){
            if(!this.style.update.have_clicked){
                this.style.update.click_label = "点击更新（60）"
                this.get_update_after()
                this.style.update.have_clicked = true
                const timer = setInterval(async()=>{
                    let remnent_count = await this.style.update.countdown - 1
                    if( remnent_count > 0){
                        this.style.update.countdown = remnent_count
                        this.style.update.click_label = `点击更新（${remnent_count}）`
                    } else {
                        clearInterval(timer)
                        this.style.update.have_clicked = false
                        this.style.update.countdown = 60
                        this.style.update.click_label = "点击更新"
                    }
                }, 1000)
            }
        }
    },
    mounted(){
        window.onresize = () => {
            this.init_style()
        }

        this.init_style()
        // set Timer
        setInterval(() =>{
            this.timer.H_M = moment().format("HH:mm")
            this.timer.S = moment().format("ss")
        }, 1000)

        new Roll(this.$refs.marqueen_bar, this.style.marqueen_bar_style, 0.4)
        new Roll(this.$refs.slogan_container, this.$refs.slogan_container.style, 1)
    }
}
</script>
<style lang="less" scoped>
    #code_page{
        position: relative;
        width: 100%;
        .bg{
            width: 100%;
            display: block;
        }
        .camera_button{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
        .marqueen{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            overflow: hidden;
            img{
                height: 100%;
                width: 200%;
                position: absolute;
            }
        }
        .slogan{
            position: absolute;
            white-space: nowrap;
            overflow: hidden;
            img{
                vertical-align: bottom;
            }
            .slogan_container{
                position: absolute;
                padding-left: 22em;
                .slogan-item{
                    padding-right: 17em;
                    &:nth-child(2){
                        padding-left: 16em;
                    }
                }
            }
        }
        .time_box{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            span{
                font-family: 'siyuan';
                font-weight: bold;
                letter-spacing: -1px;
                color: #3f59e5
            }
        }
        .hours_certification{
            position: absolute;
            font-family: 'siyuan_normal';
            color: #fff
        }
        .update{
            position: absolute;
            font-family: 'siyuan_normal';
        }
        .update_button{
            position: absolute;
        }
        .personal_name{
            position: absolute;
            font-family: 'siyuan_normal';
        }
        .personal_id{
            position: absolute;
            font-family: 'siyuan_normal';
            color: #919eab
        }
        .multi_functions{
            position: absolute;
            display: flex;
            flex-flow: row wrap;
            justify-content: space-between;
            align-items: center;
            .funtions_item{
                width: 26%;
                height: 47%;
            }
        }
    }
</style>