<template lang="">
    <div id="qrcode_place">
        <img class="bg" src="../assets/images/qrcode-place.png">
        <div class="location" :style="style.location_style">{{ info.location }}</div>
        <div class="location_type" :style="style.location_type_style">{{ info.location_type }}</div>
        <div class="location_category" :style="style.location_category_style">{{ info.location_category }}</div>
        <div class="location_detail" :style="style.location_detail_style">{{ info.location_detail }}</div>
        <div class="now_time" :style="style.now_time_style">{{ now_time }}</div>
        <div class="personal_info" :style="style.personal_name_style">{{ info.personal_name }}</div>
        <div class="personal_info" :style="style.personal_id_style">{{ info.personal_id }}</div>
        <div class="personal_info" :style="style.personal_phone_number_style">{{ info.personal_phone_number }}</div>
    </div>
</template>
<script>
import moment from "moment"

export default {
    data(){
        return {
            info:{
                location: "汇点广场1B座",
                location_type: "其他",
                location_category: "四川省/成都市/武侯区",
                location_detail: "汇点广场1座",
                personal_name: "*亮",
                personal_id: "51**************36",
                personal_phone_number: "185*****856"
            },
            ratio: 2.74,
            now_time: moment().format("YYYY-MM-DD HH:mm:ss"),
            dimension: {
                background_img_width: 1080,
                background_img_height: 2400,
            },
            style: {
                location_style: {},
                location_type_style: {},
                location_category_style: {},
                location_detail_style: {},
                now_time_style: {},
                personal_name_style: {},
                personal_id_style: {},
                personal_phone_number_style: {},
            }
        }
    },
    methods: {
        CR(len){
            return (len / this.ratio) + "px"
        },
        init_style(){
            // get_ratio
            this.ratio = this.dimension.background_img_width / window.outerWidth
            this.get_location_style()
            this.get_location_type_style()
            this.get_location_category_style()
            this.get_location_detail_style()
            this.get_now_time_style()
            this.get_personal_name_style()
            this.get_personal_id_style()
            this.get_personal_phone_number_style()
        },
        get_personal_phone_number_style(){
            this.style.personal_phone_number_style = {
                top: this.CR(2070),
                right: this.CR(115),
                fontSize: this.CR(45)
            }
        },
        get_personal_id_style(){
            this.style.personal_id_style = {
                top: this.CR(1952),
                right: this.CR(115),
                fontSize: this.CR(45)
            }
        },
        get_personal_name_style(){
            this.style.personal_name_style = {
                top: this.CR(1838),
                right: this.CR(115),
                fontSize: this.CR(45)
            }
        },
        get_now_time_style(){
            this.style.now_time_style = {
                top: this.CR(803),
                left: this.CR(114),
                fontSize: this.CR(45)
            }
        },
        get_location_detail_style(){
            this.style.location_detail_style = {
                top: this.CR(511),
                left: this.CR(114),
                fontSize: this.CR(45)
            }
        },
        get_location_category_style(){
            this.style.location_category_style = {
                top: this.CR(451),
                left: this.CR(114),
                fontSize: this.CR(45)
            }
        },
        get_location_type_style(){
            this.style.location_type_style = {
                top: this.CR(390),
                left: this.CR(114),
                fontSize: this.CR(42)
            }
        },
        get_location_style(){
            this.style.location_style = {
                top: this.CR(285),
                left: this.CR(114),
                fontSize: this.CR(67)
            }
        }
    },
    mounted(){
        window.onresize = () => {
            this.init_style()
        }
        this.init_style()
        setInterval(async() => {
            this.now_time = await moment().format("YYYY-MM-DD HH:mm:ss")
        }, 1000)
    }
}
</script>
<style lang="less" scoped>
    #qrcode_place{
        position: relative;
        .bg{
            display: block;
            width: 100%;
        }
        .location{
            position: absolute;
            font-family: 'siyuan';
            font-weight: bold;
            color: #1d1f24
        }
        .location_type{
            position: absolute;
            font-family: 'siyuan_normal';
            color: #ff9838
        }
        .location_category{
            position: absolute;
            font-family: 'siyuan_regular';
            color: #333333
        }
        .location_detail{
            position: absolute;
            font-family: 'siyuan_regular';
            color: #919eab
        }
        .now_time{
            position: absolute;
            font-family: 'siyuan_regular';
            color: #919eab
        }
        .personal_info{
            position: absolute;
            font-family: 'siyuan_regular';
            color: #000
        }
    }
</style>