<template lang="">
    <div id="hospital_page">
        <img class="bg" src="../assets/images/hospital_page.png">
        <div class="checktime" :style="style.checktime">
            {{date.checktime}} 18时
        </div>
        <div class="checktime" :style="style.result_time">
            {{date.result_time}} 0时
        </div>
        <div class="checktime" :style="style.hospital_name">
            {{info.hospital_name}}
        </div>
        <div class="person" :style="style.person">
            {{info.personal_name}} {{info.personal_id}}
        </div>
    </div>
</template>
<script>
import moment from "moment"

export default {
    data(){
        return {
            ratio: 2.74,
            info:{
                personal_name: "*亮",
                personal_id: "51**************36",
                hospital_name: "成都现代医院"
            },
            dimension: {
                background_img_width: 1080,
                background_img_height: 2400,
            },
            style: {
                checktime: {},
                result_time: {},
                hospital_name: {},
                person: {}
            },
            date:{
                checktime: moment().subtract(1, 'days').format('YYYY-MM-DD'),
                result_time: moment().format("YYYY-MM-DD")
            },
            hospital_name: ""
        }
    },
    methods:{
        CR(len){
            return (len / this.ratio) + "px"
        },
        init_style(){
            // get_ratio
            this.ratio = this.dimension.background_img_width / window.outerWidth
            // style
            this.get_checktime_style()
            this.get_result_time_style()
            this.get_hospital_name()
            this.get_person()
        },
        get_checktime_style(){
            this.style.checktime = {
                top: this.CR(1005),
                left: this.CR(464),
                fontSize: this.CR(45),
                display: "block"
            }
        },
        get_result_time_style(){
            this.style.result_time = {
                top: this.CR(1083),
                left: this.CR(476),
                fontSize: this.CR(45),
                display: "block"
            }
        },
        get_hospital_name(){
            this.style.hospital_name = {
                top: this.CR(1141),
                left: '50%',
                transform: 'translateX(-50%)',
                fontSize: this.CR(45),
                display: "block"
            }
        },
        get_person(){
            this.style.person = {
                top: this.CR(340),
                left: this.CR(70),
                fontSize: this.CR(45),
                display: "block"
            }
        }
    },
    mounted(){
        window.onresize = () => {
            this.init_style()
        }
        this.init_style()
    }
}
</script>
<style lang="less" scoped>
    #hospital_page{
        position: relative;
        .bg{
            width: 100%;
            display: block;
        }
        .checktime{
            position: absolute;
            font-family: 'siyuan_normal';
            color: "#2b2b2b";
            display: none;
        }
        .person{
            position: absolute;
            font-family: 'siyuan_normal';
            color: #222222;
            display: none;
        }
    }
</style>