import { createRouter, createWebHashHistory } from "vue-router"
import health_page from "../components/health-page.vue"
import hospital_page from "../components/hospital-page.vue"
import app_cover from "../components/app-cover.vue"
import scan from "../components/scan-qrcode.vue"
import place from "../components/qrcode-place.vue"

const routes = [
    {
        path: "/health_page",
        name: "health_page",
        component: health_page
    },
    {
        path: "/hospital_page",
        name: "hospital_page",
        component: hospital_page
    },
    {
        path: "/",
        name: "app_cover",
        component: app_cover
    },
    {
        path: "/scan",
        name: "scan",
        component: scan
    },
    {
        path: "/place",
        name: "place",
        component: place
    }
]

const router = createRouter({
    routes,
    history: createWebHashHistory()
})

router.beforeEach((to, from, next) => {
    if(to.name == "app_cover" && from.name == "health_page"){
        return
    }
    next()
})

export default router